import React from "react";
import book from "./img/book.png";

const StartPage = props => {
  return (
    <div className="App">
      <body id="home" data-spy="scroll" data-target="#main-nav">
        <header className="">
          <div>
            <section id="showcase" className="py-5">
              <div className="primary-overlay text-white">
                <div className="container">
                  <div className="row">
                    <div className="col-lg-6 text-center">
                      <h1 className="display-2 mt-5 pt-5">
                        Do What You Dream Of...
                      </h1>
                      <p className="lead">
                        To dream of new technology represents a wish to make
                        your life easier or more efficient......
                      </p>
                      <a
                        href="#"
                        className="btn btn-outline-secondary btn-lg text-white"
                      >
                        <i className="fas fa-arrow-right"></i> Read More
                      </a>
                    </div>
                    <div className="col-lg-6">
                      <img
                        src={book}
                        alt=""
                        className="img-fluid d-none d-lg-block"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </section>

            <section id="boxes" className="py-5">
              <div className="container">
                <div className="row">
                  <div className="col-md-3">
                    <div className="card text-center border-primary mb-resp">
                      <div className="card-body">
                        <h3 className="text-primary">Be Better</h3>
                        <p className="text-muted">
                          Technology has affected the different industries
                          immensely.
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="card text-center bg-primary text-white mb-resp">
                      <div className="card-body">
                        <h3>Be Smarter</h3>
                        <p>
                          Everything you need to get started with SMART in your
                          classroom today.
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="card text-center border-primary mb-resp">
                      <div className="card-body">
                        <h3 className="text-primary">Be Faster</h3>
                        <p className="text-muted">
                          Lorem ipsum dolor, sit amet consectetur adipisicing
                          elit. Eveniet, deserunt.
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="card text-center bg-primary text-white">
                      <div className="card-body">
                        <h3>Be Stronger</h3>
                        <p>
                          From schools to colleges and universities, everyone
                          can feel the impact of technology.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </div>
        </header>
      </body>
    </div>
  );
};

export default StartPage;
